@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Farro:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Farro:wght@300&family=Roboto+Mono:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Farro:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Farro:wght@300&family=Roboto+Mono:wght@200&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
}

.nav-link {
  font-family: "Farro", sans-serif;
  text-transform: uppercase;
  padding-top: 1.2rem;
}

.navbar-light .navbar-nav .nav-link {
  color: whitesmoke;
  font-size: larger;
  padding-left: 2rem;
}

.navbar-light .navbar-nav .nav-link:hover {
  color: yellow;
}

.navbar-light .navbar-nav .nav-link:active {
  color: yellow;
}

.navbar-light .navbar-nav .active > .nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show > .nav-link {
  color: yellow;
  font-weight: bold;
}

.bg-info {
  background-color: #1f1724 !important;
}

.navbar-dark .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;..");
}

/* width */
::-webkit-scrollbar {
  width: 13px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: lightcoral;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.header-image {
  margin-top: 2rem;
  position: relative;
  background: url(/static/media/bg-red-computer.4020978e.jpg);
  background-size: cover;
  height: 98vh;
  width: 100%;
}

.header-content {
  padding-top: 10rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: "Roboto Mono", monospace;
  color: white;
}

.changing-text {
  color: bisque;
  font-weight: bolder;
  font-size: 2rem;
}

.btn-dark:hover {
  background-color: bisque;
  color: black;
}

.skills {
  font-family: "Farro", sans-serif;
  background-color: #f8f9fa;
  height: 150%;
}

.right-bar-skills {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

.left-bar-skills {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

.lh-card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 2rem;
  height: 14.6875rem;
  width: 90%;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.lh-card:hover {
  box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
  border: 1px solid black;
}

.image-left-card {
  height: 100%;
  width: 40%;
  position: relative;
  background-position: center;
  padding: 10px;
}

.card-content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.card-content-header {
  padding-left: 15px;
}

@media only screen and (max-width: 520px) {
  .card-content-header {
    padding-left: 0px;
  }
}

.square-card {
  height: 251px;
  width: 251px;
  background-color: #ffffff;
  margin: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 12px;
  padding: 5px;
}

.square-image {
  height: 56%;
  width: 80%;
  background-position: center;
  margin-bottom: 1rem;
}

.contact-section {
  height: 60vh;
  font-family: "Roboto Mono", monospace;
  background-color: lightpink;
}

.main {
  height: 15rem;
  width: 40rem;
  background-color: #ffffff;
  border-radius: 30px;
}

.main {
  height: 15rem;
  width: 39rem;

  border-radius: 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.contact-card {
  font-size: 0.2rem;
}

.right-bar {
  margin-left: -2.6rem;
}


